import { toggleMark } from 'prosemirror-commands'
import { EditorState } from 'prosemirror-state'
import { EditorView } from 'prosemirror-view'
import { BasePlugin } from './BasePlugin'

/**
 * Plugin for strikethrough text formatting
 */
export default class StrikethroughPlugin extends BasePlugin {
  /**
   * The name of the plugin
   */
  public readonly name = 'strikethrough'

  /**
   * Create a new StrikethroughPlugin instance
   */
  constructor() {
    super({
      toolbarButton: {
        icon: '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><path d="M17 9V6a2 2 0 0 0-2-2H9a2 2 0 0 0-2 2v3"></path><path d="M16 15v3a2 2 0 0 1-2 2H9a2 2 0 0 1-2-2v-3"></path><line x1="4" y1="12" x2="20" y2="12"></line></svg>',
        tooltip: 'Strikethrough (Ctrl+Shift+X)',
        action: (view: EditorView) => this.toggleStrikethrough(view),
        isActive: (state: EditorState) => this.isActive(state, 'strikethrough')
      },
      keymap: {
        'Mod-Shift-x': (state, dispatch) => toggleMark(state.schema.marks.strikethrough)(state, dispatch)
      }
    })
  }

  /**
   * Toggle strikethrough formatting
   * @param view The editor view
   * @returns Whether the action was successful
   */
  private toggleStrikethrough(view: EditorView): boolean {
    const { state, dispatch } = view
    return toggleMark(state.schema.marks.strikethrough)(state, dispatch)
  }
}
